body {
  margin: 0;
  font-family: 'Oxygen', sans-serif;
  /* background-color: #e8f1f9; */
}
.text-muted {
  color: #1f252f !important;
  font-weight: 400 !important; 
}
.text-dark {
  color: #1f252f !important;
}

.font-size-12 {
  font-size:12px
}

.bold {
  font-weight: bold;
}

.semi-bold {
  font-weight: 400 !important;
  color: #4c5159;
}

.flag-icon {
  width:20px
}

.dropdown {
  text-align: left;
}
.dropdown .btn-secondary {
  border: none;
  background-color: #ffff;
}

.dropdown .btn-secondary:focus {
  
  outline: none;
  border-style: none;
  outline: none;
}
