.certificado_view {
min-width:195px;
background-color: #e8f1f9;
min-height:140px;
border-radius: 8px;
display: flex;
justify-content: center;
text-align: center;
align-items: center;
flex-direction: column;
margin-top: 30px;
color: #8d8d8d;
font-weight: 400;
}

.certificado_view img {
  padding: 5px;
  transition: opacity 200ms ease;
  cursor: all-scroll;
 
}

.certificado_view img:hover {
  transform: scale(3);
}

.phone-icon {
  padding-right: 5px;
  width: 40px;
}